class ImgUtils {
  static load(src) {
    return new Promise((resolve, reject) => {
      const imgNode = new Image();
      imgNode.crossOrigin = "anonymous";
      if (src.startsWith("file")) {
        imgNode.src = encodeURI(src);
      } else {
        imgNode.src = src;
      }
      imgNode.onload = () => {
        resolve(imgNode);
      };
      imgNode.onerror = () => {
        reject(imgNode);
      };
    });
  }
}

export { ImgUtils as default };
