class PerspectiveShiftUtils {
  static translationMatrix(x, y, z) {
    return [1, 0, 0, x, 0, 1, 0, y, 0, 0, 1, z, 0, 0, 0, 1];
  }
  static frontViewMatrix4(distance) {
    return this.translationMatrix(distance, 0, 0);
  }
  static backViewMatrix4(distance) {
    return this.translationMatrix(-distance, 0, 0);
  }
  static leftViewMatrix4(distance) {
    return this.translationMatrix(0, distance, 0);
  }
  static rightViewMatrix4(distance) {
    return this.translationMatrix(0, -distance, 0);
  }
  static topViewMatrix4(distance) {
    return this.translationMatrix(-0.01, 0, distance);
  }
  static leftFrontTopViewMatrix4(scale, volume) {
    const offsetX = volume.width / 2;
    const offsetY = volume.height / 2;
    const offsetZ = volume.depth / 2;
    return this.translationMatrix(offsetX * scale, offsetY * scale, offsetZ * scale);
  }
  static rightBackTopViewMatrix4(scale, volume) {
    const offsetX = -volume.width / 2;
    const offsetY = -volume.height / 2;
    const offsetZ = volume.depth / 2;
    return this.translationMatrix(offsetX * scale, offsetY * scale, offsetZ * scale);
  }
}

export { PerspectiveShiftUtils as default };
