class DefaultSelector {
  constructor(store) {
    this.store = store;
    this.polygon2d = [];
  }
  pushPoint(x, y) {
    this.polygon2d.push({x, y});
    this.store.syncPolygon2d(this.polygon2d);
  }
  updatePolygon2d(polygon2d) {
    this.polygon2d = polygon2d;
    this.store.syncPolygon2d(this.polygon2d);
  }
}

export { DefaultSelector as default };
