import { INVALID_COLOR } from './index.js';

const BORDER_OPACITY_LEVEL = {
  0: 0,
  1: 0.2,
  3: 0.4,
  5: 0.6,
  7: 0.8,
  9: 1,
  10: 1
};
const FILL_OPACITY_LEVEL = {
  0: 0,
  1: 0,
  3: 0.2,
  5: 0.4,
  7: 0.6,
  9: 0.8,
  10: 1
};
const _ToolStyleUtils = class {
  static rgbaStringToHex(rgbaString) {
    const rgbaArr = rgbaString.substring(5, rgbaString.length - 1).replace(/ /g, "").split(",");
    const r = parseInt(rgbaArr[0]);
    const g = parseInt(rgbaArr[1]);
    const b = parseInt(rgbaArr[2]);
    const a = parseFloat(rgbaArr[3]);
    const hexR = r.toString(16).padStart(2, "0");
    const hexG = g.toString(16).padStart(2, "0");
    const hexB = b.toString(16).padStart(2, "0");
    const hexA = Math.round(a * 255).toString(16).padStart(2, "0");
    return "#" + hexR + hexG + hexB + hexA;
  }
};
let ToolStyleUtils = _ToolStyleUtils;
ToolStyleUtils.replaceAFromRGBA = (rgba, opacity) => {
  return rgba.split(" ").join("").replace(/,[0-9]+([.]{1}[0-9]+){0,1}\)/, `,${opacity.toFixed(2)})`);
};
ToolStyleUtils.toRGBAArr = (rgbStr, opacity = 1) => {
  const match = rgbStr.match(/\d+/g);
  if (match) {
    const [r, g, b, a] = match;
    return [r, g, b, a];
  }
};
ToolStyleUtils.getToolStrokeAndFill = (color, options) => {
  const {
    hover = false,
    selected = false,
    borderOpacity = 1,
    fillOpacity = 1,
    commonOpacity = 0.8
  } = options;
  if (selected || hover) {
    return {
      stroke: _ToolStyleUtils.replaceAFromRGBA(color, borderOpacity),
      fill: _ToolStyleUtils.replaceAFromRGBA(color, fillOpacity),
      rgba: _ToolStyleUtils.toRGBAArr(color)
    };
  }
  return {
    stroke: _ToolStyleUtils.replaceAFromRGBA(color, borderOpacity * commonOpacity),
    fill: _ToolStyleUtils.replaceAFromRGBA(color, fillOpacity * commonOpacity),
    rgba: _ToolStyleUtils.toRGBAArr(color)
  };
};
ToolStyleUtils.getStrokeAndFill = (color, opacity = 1, fillOpacity = 1) => {
  return {
    stroke: _ToolStyleUtils.replaceAFromRGBA(color, opacity),
    fill: _ToolStyleUtils.replaceAFromRGBA(color, fillOpacity)
  };
};
ToolStyleUtils.getToolColorList = (color, borderOpacityParam = 9, fillOpacityParam = 9) => {
  const borderOpacity = BORDER_OPACITY_LEVEL[borderOpacityParam];
  const fillOpacity = FILL_OPACITY_LEVEL[fillOpacityParam];
  return {
    valid: _ToolStyleUtils.getStrokeAndFill(color, 1 * borderOpacity, 0.4 * fillOpacity),
    invalid: _ToolStyleUtils.getStrokeAndFill(INVALID_COLOR, 1 * borderOpacity, 0.8 * fillOpacity),
    validSelected: _ToolStyleUtils.getStrokeAndFill(color, 1 * borderOpacity, 0.8 * fillOpacity),
    invalidSelected: _ToolStyleUtils.getStrokeAndFill(INVALID_COLOR, 1 * borderOpacity, 0.24 * fillOpacity),
    validHover: _ToolStyleUtils.getStrokeAndFill(color, 1 * borderOpacity, 0.64 * fillOpacity),
    invalidHover: _ToolStyleUtils.getStrokeAndFill(INVALID_COLOR, 1 * borderOpacity, 0.4 * fillOpacity)
  };
};
ToolStyleUtils.getAttributeIndex = (attribute, attributeList) => {
  try {
    const attributeIndex = attributeList.findIndex((i) => i.value === attribute);
    return attributeIndex;
  } catch (error) {
    return -1;
  }
};

export { ToolStyleUtils as default };
